import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const ProtectedRoute = ({ children }) => {
  const navigate = useNavigate();
  const [authorized, setAuthorized] = useState(null);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const res = await fetch("http://34.64.211.3:5003/api/inventory/protected", {
          credentials: "include",
        });

        if (res.status === 401) {
          const reissue = await fetch("http://34.64.211.3:5080/api/reissue", {
            method: "POST",
            credentials: "include",
          });

          if (!reissue.ok) throw new Error(" ");

          const retry = await fetch("http://34.64.211.3:5003/api/inventory/protected", {
            credentials: "include",
          });

          if (!retry.ok) throw new Error(" ");
          const data = await retry.json();
          if (data.role !== "admin") throw new Error(" ");

          setAuthorized(true);
        } else {
          const data = await res.json();
          if (data.role !== "admin") throw new Error(" ");

          setAuthorized(true);
        }
      } catch (err) {
        alert("    .");
        window.location.href = "http://34.64.211.3:3000/";
      }
    };

    checkAuth();
  }, [navigate]);

  if (authorized === null) return <div> ...</div>;
  return children;
};

export default ProtectedRoute;
